// 
import React, { Component } from 'react';
import { Row, Col } from 'antd';
import '../styles.less'
export default class BasicInfo extends Component {
  constructor(props){
    super(props);
    this.state = {
        code:'091392',
        specialty:'',
        studyTime:'64',
        keepTime:'3.76',
        classType:'',
        property:'',
        classGroup:'',
        peopleNum:'10'
    }
  }
  render() {
    return (
      <Row type="flex" align="middle" style={{paddingLeft:'10%'}}>
        <Col span={24}  style={{margin:0}} className={'major_visual_basic_info_title'}></Col>
        <Col span={24} className={'major_visual_basic_info_content'}>
          <div><label></label><span>{this.state.code}</span></div>
          <div><label></label><span>{this.state.specialty}</span></div>
          <div>
              <label></label><span>{this.state.studyTime}</span>
              <label style={{marginLeft:'20px'}}></label><span>{this.state.keepTime}</span>
          </div>
          <div>
              <label></label><span>{this.state.classType}</span>
              <label style={{marginLeft:'20px'}}></label><span>{this.state.property}</span>
          </div>
          <div>
              <label></label><span>{this.state.classGroup}</span>
              <label style={{marginLeft:'20px'}}></label><span>{this.state.peopleNum}</span>
          </div>
        </Col>
      </Row>
    )
  }
}
